import Layout from '../components/MyLayout.js'
import Link from 'next/link'


const PostLink = (props) => (
  <li>
    <Link as={`/p/${props.id}`} href={`/post?title=${props.title}`}>
      <a>{props.title}</a>
    </Link>
  </li>
)

export default () => (
  <Layout>
    <h1>Share what you have brought for u's love</h1>
    <div className="container">
    <form>
        <div className="form-group">
            <label htmlfor="title">Caption</label>
            <input type="text" className="form-control" id="title" placeholder="Write something..."/>
        </div>
        <div className="form-group">
            <label htmlfor="photo">File input</label>
            <input type="file" id="photo" />
            <p className="help-block">Upload photo (Optional).</p>
        </div>
        <button type="submit" className="btn btn-default">Submit</button>
        </form>
    </div>
  </Layout>
)


